<template>
  <div class="seePage">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>库存管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">调整成本价</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看调整成本价</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" :model="TableData" class="demo-form-inline">
        <el-form-item label="销售组织：">
          <p style="width:170px;">{{TableData.info.company_name}}</p>
        </el-form-item>
        <el-form-item label="单据号：">
          <p style="width:170px;">{{TableData.info.adjust_sn}}</p>
        </el-form-item>
        <el-form-item label="业务日期：">
          <p style="width:170px;">{{TableData.info.business_time}}</p>
        </el-form-item>
        <el-form-item label="备注：">
          <p style="width:170px;">{{TableData.info.remark}}</p>
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.goods_list" style="width: 100%" max-height="600">
        <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44" :reserve-selection="false">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号" min-width="160"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="goods_name" label="商品名称" min-width="500">
          <template slot-scope="scope">
            <pre>{{scope.row.goods_name}}</pre>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="batch" label="年周/批次" min-width="120">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="warehouse_name" label="仓库名称" width="132">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="allocation_name" label="货位名称" min-width="150">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="stocks" label="结存数量" min-width="150" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="cost_price" label="结存成本价" min-width="150" align="right">
          <template slot-scope="scope">
            <pre>{{scope.row.cost_price  | priceFormatNone}}</pre>
          </template>
        </el-table-column>
        <!-- 取消使用过滤字段 -->
        <!-- <el-table-column :show-overflow-tooltip="true" property="cost_balance" label="结存成本总价" min-width="150" align="right" :formatter="cost_balance">
        </el-table-column> -->
      <el-table-column :show-overflow-tooltip="true" property="cost_balance" label="结存成本总价" min-width="150" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="adjust_price" label="调整后成本价" min-width="150" align="right">
          <template slot-scope="scope">
            <pre>{{scope.row.adjust_price  | priceFormatNone}}</pre>
          </template>
        </el-table-column>

        <el-table-column :show-overflow-tooltip="true" property="adjust_balance" label="调整后结存成本总价" min-width="150" align="right">
          <template slot-scope="scope">
            <pre>{{scope.row.adjust_balance  | priceFormatNone}}</pre>
          </template>
        </el-table-column>

        <el-table-column :show-overflow-tooltip="true" label="出库成本总差价" min-width="150" align="right" :render-header="renderHeader">
          <template slot-scope="scope">
            <!-- {{ (scope.row.stocks*scope.row.cost_price - scope.row.stocks*scope.row.adjust_price || 0 )| priceFormat}} -->
            {{ Number(scope.row.cost_balance - scope.row.adjust_balance ).toFixed(2).toString() .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",") }}
          </template>
        </el-table-column>

        <el-table-column :show-overflow-tooltip="true" property="update_time" label="最近修改时间" min-width="170">
        </el-table-column>

        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注" min-width="120">
        </el-table-column>
      </el-table>
    </div>
    <div class="page_container">
      <div class="pageNumber">每页显示</div>
      <el-pagination class="erp-pagination" @size-change="handleSizeChange" :page-size="10" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
      </el-pagination>
    </div>
    <div class="dialog-footer" style="text-align:center;margin-top:20px;">
      <!-- 未审核 -->
      <template v-if="TableData.info.auditor_status == 1">
        <el-button type="primary" @click="audit('adjustCostAuidtorPass')" v-if="authority.b_pass==1">审核</el-button>
        <el-button @click="audit('adjustCostAuditorInvalid')" v-if="authority.b_invalid==1">作废</el-button>
      </template>
      <!-- 作废 -->
      <template v-if="TableData.info.auditor_status == 2 || TableData.info.auditor_status == 3 || (authority.b_invalid != 1 && authority.b_pass != 1)">
        <el-button @click="goBack">返回</el-button>
      </template>
      <el-button @click="goBack" v-else>取消</el-button>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
export default {
  name: 'instorageAdjustcostAdjustcostSee',
  data() {
    return {
      authority: {},
      TableData: {
        info: {}
      },
      params: {},
      per_page: [30, 50, 100, 500]
    };
  },
  computed: {
    ...mapGetters(["adjustcostIndexGet", "adjustcostSeeGet"])
  },
  methods: {
    ...mapActions(["adjustcostIndexACT"]),
    // 暂时不使用该字段
    // cost_balance(row, column, cellValue, index) {
    //   row.cost_balance = row.cost_price * row.stocks;
    //   return row.cost_balance
    //     .toFixed(2)
    //     .toString()
    //     .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
    // },
    goBack() {
      this.$router.push({
        path: "/inventory/adjustcost"
      });
      this.adjustcostIndexACT({
        goPage: this.adjustcostSeeGet.goPage,
        params: this.adjustcostSeeGet.params,
        pageSize: this.adjustcostSeeGet.pageSize
      });
    },
    renderHeader() {
      return (
        <el-tooltip class="item" effect="dark" placement="top">
          <p slot="content">
            1、出库成本总差价=结存成本总价-调整后结存成本总价
            <br />
            <br />
            2、出库成本总差价&gt;0，表示降低总成本价；
            <br />
            <br />
            3、出库成本总差价&lt;0，表示调高总成本价；
          </p>
          <span
            class="el-icon-question"
            style="cursor:pointer;font-weight:bold"
          >
            出库成本总差价
          </span>
        </el-tooltip>
      );
    },
    initData() {
      this.$axMethod.infoAdjustGoods(this.params).then(({ data }) => {
        if (data.code == 200) {
          this.TableData = data.data;
        }
      });
    },
    audit(api) {
      this.$axios({
        method: "post",
        url: this.$api[api],
        data: {
          adjust_id: this.params.adjust_id
        }
      }).then(res => {
        if (res.data.code == 200) {
          this.$message({
            message: res.data.data,
            type: "success"
          });
          this.goBack();
        }
      });
    },
    handleSizeChange(val) {
      this.params.per_page = val;
      this.initData();
    },
    handleCurrentChange(val) {
      this.params.page = val;
      this.initData();
    }
  },
  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.params = { adjust_id: this.adjustcostSeeGet.adjust_id };
    this.initData();
  }
};
</script>

<style scoped>
.title {
  line-height: 50px;
  border-bottom: 1px solid #dddddd;
}

.el-form-item {
  margin-right: 50px;
}
.tips li {
  float: right;
  line-height: 50px;
  margin-left: 15px;
}
</style>